import React, { useState } from "react";
import "./AddUser.css";
import Modal from "./Modal";

const AddUser = (props) => {
  const [userName, setUserName] = useState("");
  const [userAge, setUserAge] = useState("");
  const [showModal, setShowModal] = useState(false);
  const [showFooter, setShowFooter] = useState();
  const [showBody, setShowBody] = useState();

  const userNameHandler = (event) => {
    setUserName(event.target.value);
  };

  const userAgeHandler = (event) => {
    setUserAge(event.target.value);
  };


  const submitHandler = (event) => {
    event.preventDefault();
    if (userName.trim().length === 0 || userAge.trim().length === 0) {
      setShowModal(true);
      setShowFooter('Invalid Input')
      setShowBody('Please enter a valid name and age(non-empty values)')
      return;
    }
    if (+userAge < 1) {
      setShowModal(true);
      setShowFooter('Invalid Age')
      setShowBody('Please enter a valid age (>0)')
      return;
    }
    const key = Math.random().toString();
    props.onAddData(userName, userAge, key);
    setUserName("");
    setUserAge("");
  };

  return (
    <div>
      <form onSubmit={submitHandler}>
        <div className="add-user">
          <label>Username</label>
          <input
            value={userName}
            type="text"
            onChange={userNameHandler}
          ></input>
          <label>Age (Years)</label>
          <input
            value={userAge}
            type="number"
            onChange={userAgeHandler}
          ></input>
          <div>
            <button type="submit" className="button">
              Add User
            </button>
          </div>
        </div>
      </form>
      <Modal show={showModal} body={showBody} footer={showFooter} onClick={()=>setShowModal(false)}/>
    </div>
  );
};

export default AddUser;
