import React, { useState} from 'react';

import AddUser from './Components/Users/AddUser';
import UserItems from './Components/Users/UserItems';

function App() {

  const [dataArray, setDataArray] = useState([]);

  const addDataHandler = (uName, uAge, uKey) => {
    setDataArray((prevArray) => {
      return [ {name: uName, age: uAge, id: uKey}, ...prevArray,]
    });
  };

  return (
    <div>
      <AddUser onAddData={addDataHandler}></AddUser>
      {(dataArray.length > 0) ? <UserItems items={dataArray}></UserItems> : '' }
    </div>
  );
}

export default App;
